.ChannelWrap {
  height: 100vh;
  width: 100vw;
  background-color: #efdecd;
  display: flex;
  flex-direction: column;
}

/****** Channel Area ******/
.ChannelUpperBar {
  flex: 1;
  display: flex;
  /* background-color: #f5f4f1; */
  border-bottom: 1.5px solid #5f686e;
  align-items: center;
}

.ChannelElemWrap {
  flex: 10;
  /* width: 100%;
  height: calc(100vh - 9vh); */
  flex-direction: row;
  display: flex;
  /* flex-flow: row nowrap; */
  /* background-color: #2679CD; */
  overflow: hidden;
}

/****** Channel leftBar ******/

.ChannelLeftSideBarWrap {
  flex: 2;
  /* margin: 0 auto; */
  /* background-color: #ffffff; */
  border-right: 1.5px solid #5f686e;
  display: flex;
  flex-direction: column;
  /* background-color: palegoldenrod; */
  /* overflow: hidden; */
}
.dragdropTitle {
  margin-left: 10px;
  margin-top: 10px;
  font-family: "REM", sans-serif;
  font-size: 20px;
}

.dropContainerWrapper {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
}

/* Drag and Drop */
.dropContainer {
  height: 70px;
  width: 180px;
  background-color: transparent;
  /* border-radius: 1.25rem; */
  display: flex;
  justify-content: center;
  align-items: center;
  /* border : 2px solid black; */

  border: 2px dashed black;
  padding: 20px;
  text-align: center;
}

.fileListWrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.fileListTitle {
  margin-left: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: "REM", sans-serif;
  font-size: 20px;
}

.inFileListWrapper {
  /* display: 1; */
  display: flex;
  position: relative;
  /* align-items: center; */
  justify-content: center;
  align-items: center; /* 수직 가운데 정렬 추가 */
  overflow: auto;
}

.inFileListWrapper::-webkit-scrollbar {
  display: none;
}

.fileList {
  width: 230px;
  height: 400px;
  border: 1px solid black;
  overflow-y: auto;
}
.fileList::-webkit-scrollbar {
  display: none;
}

.progressContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background-color: rgba(255, 255, 255, 0.8); progress가 가려지는 것을 가시성 좋게 하기 위해 배경 추가 */
  z-index: 1; /* ListItem 아래에 배치하기 위해 z-index 설정 */
}

