/*********** ChannelMain *********/
.ChannelMainWrap {
  flex: 6;
  height: auto;
  /* overflow: auto; */
  /* margin: 0 auto; */
  /* background-color: #f5f4f1; */
  /* background-color: palegoldenrod; */
  overflow: auto;
}

.ChannelMainWrap::-webkit-scrollbar {
  display: none;
}

.dumpChat {
  position: absolute;
  /* background-color: red; */
  /* background-color: #f5f4f1; */

  height: 100px;
  width: 100%;
  margin-top: auto;
  z-index: 0;
}

/**************  ChannelRightSideBar ****************/

.ChannelRightSideBarWrap {
  flex: 2;
  border-left: 1px solid black;
  /* height: 100%; */
  /* margin: 0 auto; */
  /* background-col or: #ffc106; */
  /* background-color: palegoldenrod; */
  /* overflow: hidden; */
}

.bookMarkTitle {
  margin-left: 10px;
  margin-top: 10px;
  font-family: "REM", sans-serif;
  font-size: 20px;
}

